/* src/App.css */

.App {
  font-family: Arial, sans-serif;
  padding: 20px;
  color: #333;
  max-width: 1000px;
  margin: 0 auto;
  background: linear-gradient(135deg, #f0f0f0, #d0e1ff);
  animation: gradient 10s ease infinite;
}

/* Profile Section */
.App-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
  border: none; /* Remove white border */
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.profile-text h1 {
  font-size: 18px;
  color: #888;
  margin: 0;
}

.profile-text h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

/* Fade-in animation */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* About Statement */
.about-statement {
  margin: 40px 0;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
  animation-delay: 0.5s;
  text-align: center; /* Center-align text */
}

.about-statement h3 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
  opacity: 0;
  transform: translateX(-100px); /* Slide in from the left */
  animation: slideIn 1.5s ease forwards;
  animation-delay: 1s;
}

/* Slide-in animation for "Math CS âž” HCI" */
@keyframes slideIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.about-statement p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
  text-align: left;
}

/* Projects Section */
.projects-section {
  margin-top: 40px;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
  animation-delay: 1s;
}

.projects-section h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: rgb(203, 182, 182);
}

.project-card {
  display: flex;
  align-items: center;
  padding: 15px;
  margin-bottom: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 150px;
  height: auto;
  margin-right: 20px;
  border-radius: 8px;
}

.project-info h3 {
  font-size: 22px;
  margin: 0;
}

.project-info p {
  font-size: 14px;
  color: #666;
}
